h2, a {
  color: #133b9e;
}
h2 {
  margin: 0;
}
a {
  font-weight: 700;
}
a:hover {
  color: #000;
}
.drawerBack h3 {
  display: inline-block;
  margin-top: 1.5em
}
.relatedProjects {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.relatedProjects div {
  text-decoration: underline;
  cursor: pointer;
  color: #133b9e;
  width: 40%;
  height: 3em;
  text-align: center;
  background: rgba(0,0,0,0.1);
  display: flex;
  justify-content: center;
  align-items: center;
}
.relatedProjects div:hover, .relatedProjects div:active {
  background: rgba(0,0,0,0.25);
}
.relatedProjects div:hover {
  color: #000;
}
.projectsTitle {
  width: 100%;
}
