@import url('https://fonts.googleapis.com/css?family=Special+Elite');

.App {
  text-align: center;
  overflow-x: hidden;
  background-image: url("./Black_Paper.png");
  background-repeat: repeat-y;
	background-size: 100vw 100vh;
}
.currently {
  cursor: pointer;
  text-decoration: underline;
  user-select: none;
}
.headline-content .currently:hover {
  color: #bbb;
}
.resume .currently:hover {
  color: #777;
}
.App-header {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	font-size: calc(10px + 2vmin);
	color: white;
}
.top-paper {
	width: 100vw;
	height: 100vh;
	background-image: url("./Dotted_Paper.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center 100%;
	position: relative;
}
.mid-paper {
	width: 100vw;
	min-height: 100vh;
  padding: 10vh 0 18vh;
	background-image: url("./Sliced_Paper.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center 50%;
	position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.headline-content {
	position: relative;
  color:#fff;
  background: #000;
  padding: 1vh;
  margin-top: -45vh;
  user-select: none;
}
.headline-content ul {
	list-style: none;
  display: flex;
  align-items: center;
}
.headline-content ul li {
  margin: 0 3vw;
}
.mrd-logo {
	position: absolute;
	top: 5vh;
	left: 37.5vw;
	width: 20vw;
	filter: none;
}
.social-link {
	filter: drop-shadow(1px 1px 3px rgba(0,0,0,.15));
}
.bottom-area {
	color: #fff;
  font-family: "Special Elite";
  margin-top: 1em;
}
.bottom-area > div {
	display: flex;
	justify-content: center;
	align-items: center;
  flex-wrap: wrap;
	width: 98vw;
  padding-bottom: 2em;
}
.social-link {
	width: 20vw;
	margin: 0 2vw;
  transition: 0.1s;
}
.social-link:hover {
  transform: scale(1.05);
}
@media only screen and (max-width: 700px) {
	.mrd-logo {
    top: 13vh;
    left: 31vw;
    width: 35vw;
	}
	.social-link {
		width: 35vw;
		margin: 2vw;
	}
}
@media only screen and (min-width: 700px) {
	.headline-content {
		margin-top: -45vh;
		margin-bottom: 25vh;
	}
  .headline-content ul {
    flex-direction: column;
  }
	.mrd-logo {
		width: 33vw;
    left: 33.5vw;
    top: 10vh;
	}
  .mid-paper {
    min-height: 0vh;
  }
  .social-link {
		width: 20vw;
	}
  .bottom-area {
    margin-top: 3em;
  }
}
@media only screen and (min-width: 1200px) {
  .top-paper {
    height:100vh;
  }
  .headline-content {
    margin-top: -50vh;
  }
	.mrd-logo {
		top: 12vh;
		left: 41vw;
		width: 17vw;
	}
  .mid-paper {
    min-height: 95vh;
  }
	.social-link:hover {
		filter: drop-shadow(2px 2px 5px rgba(0,0,0,0.25));
		cursor: pointer;
	}
	.social-link {
		width: 15vw;
    margin: 0 2vw;
	}
}
